\n\n  &:hover, &:focus, &:active {\n    border-bottom: 3px solid $color-green;\n  }\n}\n\nul {\n  list-style-type: lower-roman;\n\n  &:not(.browser-default) {\n    padding-left: 30px;\n\n    & > li {\n      list-style-type: disc;\n      font-weight: 300;\n    }\n  }\n}\n\nstrong, b {\n  font-weight: 500;\n}\n","* {\n  box-sizing: border-box;\n}\n\n.container {\n  @media #{$medium-and-up} {\n    width: 95%;\n  }\n  @media #{$large-and-up} {\n    width: 70%;\n  }\n}\n\n.content {\n  position: relative;\n  z-index: 3;\n}\n\n.flex {\n  display: flex;\n\n  @media #{$small-and-down} {\n    flex-wrap: wrap;\n  }\n}\n\n.flex-center {\n  align-items: center;\n}\n\n.flex-end {\n  justify-content: flex-end;\n}\n\n.flex-bottom {\n  align-items: flex-end;\n}\n\n.flex-wrap {\n  flex-wrap: wrap;\n}\n\n.flex-grid {\n  display: flex;\n  flex-wrap: wrap;\n\n  .flex-item {\n    padding: 10px;\n  }\n\n  &.s1x {\n    .flex-item {\n      display: flex;\n      position: relative;\n\n      @media #{$xsmall-and-down} {\n        width: 100%;\n      }\n    }\n  }\n\n  &.m2x {\n    .flex-item {\n      display: flex;\n      position: relative;\n\n      @media #{$small-and-up} {\n        width: 50%;\n      }\n    }\n  }\n}\n\n.space-between {\n  justify-content: space-between;\n}\n\n.space-evenly {\n  justify-content: space-evenly;\n}\n\n.float-left {\n  @extend .left;\n}\n\n.float-center {\n  margin-left: auto;\n  margin-right: auto;\n}\n\n.absolute-right {\n  position: absolute;\n  right: 0;\n}\n\n.relative {\n  position: relative;\n}\n\n.mega-link.mega-link {\n  display: block;\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  z-index: 5;\n}\n\n[type=\"checkbox\"]:not(:checked), [type=\"checkbox\"]:checked {\n  position: static;\n  opacity: 1;\n  pointer-events: auto;\n}\n\n.wpcf7-checkbox {\n  column-count: 2;\n  display: block;\n}\n\nspan.wpcf7-list-item {\n  display: block !important;\n}\n\n@media #{$xsmall-and-down} {\n  .row .col.xs12 {\n    width: 100%;\n  }\n}\n\nimg {\n  max-width: 100%;\n  height: auto;\n}\n","// Main Button Styling\n.btn {\n  margin: 10px 0;\n  background: $color-white;\n  height: 45px;\n  line-height: 43px;\n  font-size: $size-regular;\n  font-family: $font-headers;\n  position: relative;\n  transition: all 0.3s;\n  border: 2px solid white;\n  border-radius: 3px;\n\n  // Arrow for hover\n  &::after {\n    content: '';\n    position: absolute;\n    opacity: 0;\n    width: 21px;\n    height: 21px;\n    top: 9px;\n    right: 5px;\n    background-repeat: no-repeat;\n    background-size: contain;\n    background-position: center;\n    background-image: url('../images/icon-arrow.svg');\n    transition: opacity 0.3s;\n  }\n\n  &:hover, &:focus, &:active {\n    color: $color-white;\n\n    &::after {\n      opacity: 1;\n    }\n  }\n\n  // Primary styles\n  &-primary {\n    color: $color-green;\n    border-color: $color-green;\n\n    &:hover, &:focus, &:active {\n      background: $color-green;\n    }\n  }\n\n  // Secondary styles\n  &-secondary {\n    color: $color-blue;\n    border-color: $color-blue;\n\n    &:hover, &:focus, &:active {\n      background: $color-blue;\n    }\n  }\n\n  // Tertiary styles\n  &-tertiary {\n    color: $color-green;\n\n    &:hover, &:focus, &:active {\n      border-color: $color-green;\n      background: $color-green;\n    }\n  }\n}\n","nav {\n  background: $color-white;\n}\n\n.logo {\n  margin-top: 10px;\n\n  img {\n    height: 65px;\n    width: auto;\n  }\n\n  &:hover {\n    border: none;\n  }\n}\n\n.nav-top {\n  padding-top: 2em;\n\n  @media #{$medium-and-down} {\n    top: 55%;\n    transform: translateY(-50%);\n  }\n}\n\n.header-homepage .nav-top {\n  padding-top: 2em;\n\n  @media #{$medium-and-down} {\n    padding-top: 4.4em;\n    top: 0;\n    transform: none;\n  }\n}\n\n.topnav-wrapper {\n  width: 80%;\n\n  .btn {\n    float: right;\n  }\n\n  @media #{$medium-and-down} {\n    width: 100%;\n\n    .btn {\n      padding: 0;\n      width: 100%;\n      margin-top: 20px;\n    }\n  }\n}\n\n.menu-top-nav-container {\n  ul {\n    display: flex;\n    margin-top: 0;\n  }\n\n  button {\n    float: right;\n  }\n}\n\n/** Main Navigation */\n.menu-main-menu-container {\n  background: $color-white;\n\n  .navbar-menu {\n    display: flex;\n    justify-content: center;\n    padding-left: 0;\n\n    & > li {\n      & > a {\n        font-family: $font-headers;\n        font-size: $size-regularmedium;\n        color: $color-blue;\n        text-align: center;\n        text-transform: uppercase;\n        padding: 0 42px;\n        position: relative;\n\n        &:hover, &:focus, &:active {\n          background: transparent;\n          color: $color-green;\n          border: none;\n        }\n\n        &::before {\n          content: '';\n          position: relative;\n          width: 25px;\n          height: 25px;\n          background-size: contain;\n          display: inline-block;\n          top: 5px;\n          right: 5px;\n        }\n      }\n\n      // Icon Hover States\n      &.icon-book a::before {\n        background-image: url('../images/icon-book.svg');\n      }\n\n      &.icon-contact a::before {\n        background-image: url('../images/icon-contact.svg');\n      }\n\n      &.icon-list a::before {\n        background-image: url('../images/icon-list.svg');\n      }\n\n      &.icon-stars a::before {\n        background-image: url('../images/icon-stars.svg');\n      }\n\n      &:hover, &:focus, &:active {\n        &.icon-book a::before {\n          background-image: url('../images/icon-book-hover.svg');\n        }\n\n        &.icon-contact a::before {\n          background-image: url('../images/icon-contact-hover.svg');\n        }\n\n        &.icon-list a::before {\n          background-image: url('../images/icon-list-hover.svg');\n        }\n\n        &.icon-stars a::before {\n          background-image: url('../images/icon-stars-hover.svg');\n        }\n      }\n    }\n\n    .menu-item-has-children {\n      position: relative;\n\n      a {\n        cursor: default;\n\n        &:hover, &:focus, &:active {\n          border: none;\n        }\n      }\n\n      .sub-menu {\n        z-index: 10;\n        background: $color-green;\n        color: $color-white;\n        transition: max-height 0.3s ease;\n        height: 0;\n        overflow: hidden;\n        width: 100%;\n        text-align: center;\n        margin: 0;\n        top: 50px;\n        padding-left: 0;\n\n        li {\n          float: none;\n          line-height: 3em;\n          list-style-type: none;\n\n          &:first-of-type {\n            padding-top: 20px;\n          }\n\n          a {\n            text-transform: uppercase;\n            font-weight: 300;\n            cursor: pointer;\n\n            &:hover, &:focus, &:active {\n              background-color: rgba(0, 0, 0, 0.1);\n            }\n          }\n        }\n      }\n\n      &:hover, &:focus, &.hover {\n        .sub-menu {\n          height: auto;\n        }\n      }\n    }\n\n    @media #{$large-and-up} {\n      .sub-menu {\n        max-height: 300em;\n        flex-direction: column;\n        position: absolute;\n\n        &::before {\n          content: '';\n          background: linear-gradient(to right top, transparent 0%, transparent 50%, white 50%);\n          position: absolute;\n          z-index: 1;\n          top: 0;\n          left: 0;\n          width: 50%;\n          height: 20px;\n        }\n\n        &::after {\n          content: '';\n          background: linear-gradient(to left top, transparent 0%, transparent 50%, white 50%);\n          position: absolute;\n          z-index: 1;\n          top: 0;\n          right: 0;\n          width: 50%;\n          height: 20px;\n        }\n      }\n    }\n    @media #{$medium-and-down} {\n      display: none;\n      margin: 0;\n      max-height: 0;\n\n      li {\n        width: 100%;\n\n        &.hover {\n          .sub-menu {\n            max-height: 300em;\n          }\n        }\n      }\n\n      .mobilenav-active & {\n        max-height: 300em;\n      }\n    }\n  }\n}\n\n@media #{$medium-and-down} {\n  // Remove main navigation\n  .topnav-wrapper .menu-top-nav-container {\n    display: none;\n  }\n\n  // Mobile Nav\n  .sticky-wrapper {\n    height: 0 !important;\n  }\n\n  .nav-primary {\n    position: fixed;\n    top: 0;\n    z-index: 100;\n    width: 100%;\n    text-align: center;\n\n    .menu-item {\n      background: $color-white;\n      padding: 10px 0;\n\n      .sub-menu li {\n        background: $color-green;\n      }\n    }\n\n    [type=\"checkbox\"] + label {\n      padding-left: 0;\n\n      &::before {\n        display: none;\n      }\n    }\n\n    .menu-trigger-wrapper {\n      height: 100%;\n      background: $color-blue;\n\n      #menu-trigger {\n        opacity: 0;\n        color: $color-white;\n      }\n    }\n  }\n}\n","// Main general social nav\n.menu-top-nav-container {\n  display: flex;\n  justify-content: flex-end;\n\n  ul {\n    padding-left: 0;\n  }\n\n  li {\n    list-style-type: none !important;\n\n    a {\n      text-indent: -1000em;\n      display: block;\n      position: relative;\n      height: 20px;\n      width: 20px;\n      margin: 0 7px;\n\n      &::before {\n        content: '';\n        position: absolute;\n        top: 0;\n        left: 0;\n        width: 20px;\n        height: 20px;\n        background-repeat: no-repeat;\n        background-size: contain;\n        background-position: center;\n      }\n\n      &:last-of-type {\n        margin-right: 0;\n      }\n\n      &:hover {\n        border: none;\n      }\n    }\n\n    &.icon-fb a::before {\n      background-image: url('../images/icon-fb.svg');\n    }\n\n    &.icon-twitter a::before {\n      background-image: url('../images/icon-twitter.svg');\n    }\n\n    &.icon-linkedin a::before {\n      background-image: url('../images/icon-linkedin.svg');\n    }\n\n    &.icon-google a::before {\n      background-image: url('../images/icon-google.svg');\n    }\n  }\n\n  @media #{$small-and-down} {\n    font-size: 10px;\n  }\n}\n\n// Social nav on footer\n.footer-contact {\n  .menu-top-nav-container {\n    justify-content: center;\n\n    li {\n      a {\n        top: 5px;\n        display: block;\n        height: 30px;\n        width: 30px;\n        margin: 0 7px;\n\n        &::before {\n          content: '';\n          width: 30px;\n          height: 30px;\n        }\n      }\n    }\n  }\n}\n","/**\n * WordPress Generated Classes\n * @see http://codex.wordpress.org/CSS#WordPress_Generated_Classes\n */\n\n/** Media alignment */\n.alignnone {\n  margin-left: 0;\n  margin-right: 0;\n  max-width: 100%;\n  height: auto;\n}\n\n.aligncenter {\n  display: block;\n  margin: ($spacer / 2) auto;\n  height: auto;\n}\n\n.alignleft,\n.alignright {\n  margin-bottom: ($spacer / 2);\n  height: auto;\n}\n\n@media (min-width: 30rem) {\n  .alignleft {\n    float: left;\n    margin-right: ($spacer / 2);\n  }\n\n  .alignright {\n    float: right;\n    margin-left: ($spacer / 2);\n  }\n}\n\n/** Captions */\n\n// TODO: .wp-caption {}\n// TODO: .wp-caption img {}\n// TODO: .wp-caption-text {}\n\n/** Text meant only for screen readers */\n.screen-reader-text {\n  position: absolute;\n  width: 1px;\n  height: 1px;\n  padding: 0;\n  margin: -1px;\n  overflow: hidden;\n  clip: rect(0, 0, 0, 0);\n  border: 0;\n  color: #000;\n  background: #fff;\n}\n",".unity-link {\n  display: inline-block;\n  max-width: 120px;\n  max-height: 23px;\n  float: right;\n\n  &:hover {\n    border: none;\n  }\n\n  @media #{$medium-and-down} {\n    float: none;\n    margin-top: 10px;\n  }\n}\n\n.svg-unity-logo {\n  width: 100%;\n  height: auto;\n  max-height: 23px;\n  display: inline-block;\n  vertical-align: middle;\n\n  path {\n    fill: $color-black;\n  }\n}\n","// Main site header banner\n#main-site-header {\n  height: 200px;\n  position: relative;\n\n  .nav-top .col {\n    text-align: center;\n  }\n}\n\n.banner {\n  position: relative;\n  background-position: center;\n  background-size: cover;\n\n  &::before {\n    content: '';\n    background: rgba($color-blue-darker, 0.6);\n\n    @include cover;\n  }\n\n  & > * {\n    position: relative;\n  }\n}\n\n.sticky-wrapper {\n  position: relative;\n  z-index: 50;\n}\n\n// Main site header title\n.page-header {\n  h1 {\n    text-align: center;\n    text-transform: uppercase;\n    padding: 1em 20px;\n    margin: 0 auto;\n    color: $color-blue;\n  }\n}\n\n// Homepage Header\n.header-homepage {\n  position: relative;\n  height: 91vh;\n  text-align: center;\n  color: white;\n\n  video {\n    position: absolute;\n    right: 0;\n    bottom: 0;\n    min-width: 100%;\n    min-height: 100%;\n    width: auto;\n    height: auto;\n    z-index: -10;\n  }\n\n  .scroll-arrow p {\n    margin: 0;\n\n    &:first-of-type {\n      transform: rotate(-90deg);\n      position: relative;\n      top: 10px;\n      font-size: $size-extralarge;\n    }\n  }\n\n  .hero {\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate3d(-50%, -50%, 0);\n    width: 80%;\n    text-align: center;\n  }\n\n  @media #{$medium-and-down} {\n    height: 100vh;\n    padding-top: 20px;\n\n    .hero {\n      top: 60%;\n      width: 95%;\n    }\n  }\n}\n","// Generic footer wrapper\nfooter {\n  background: $color-blue;\n  margin-top: 3em;\n\n  // Logo/accolades rows\n  .footer-logos {\n    display: flex;\n    justify-content: space-between;\n    background: $color-white;\n\n    .widget_media_image {\n      flex-grow: 1;\n      padding: 2em 0;\n      text-align: center;\n\n      .image {\n        max-height: 75px !important;\n        width: auto;\n      }\n    }\n  }\n\n  // Footer contact section\n  .footer-contact {\n    display: flex;\n    align-items: center;\n    color: white;\n\n    img {\n      height: 66px;\n      width: auto;\n    }\n\n    @media #{$small-and-down} {\n      display: block;\n    }\n  }\n\n  // Copyright row\n  .footer-copyright {\n    background: $color-white;\n    padding: 10px 0;\n\n    .row {\n      margin: 0;\n    }\n\n    .p